import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';

import MaterialTable from 'material-table';
import { withStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';

import { configShopSetSelectedRows, resetFilters } from '../../redux/config-shop/config-shop.actions';

const CustomTable = ({
  tableData,
  page,
  configShopSetSelectedRows,
  filteredConfigShopData,
  configShopfilters,
  configShopPurchasedfilters,
  filteredConfigShopPurchasedData,
  resetFilters
}) => {
  const tableRef = React.createRef();

  useEffect(() => {
    tableRef.current.onAllSelected(false);
    resetFilters()
  }, []);

  const theme = useTheme();
  const StyledMaterialTable = withStyles(() => ({}))((props) => {
    return (
      <MaterialTable


        options={{
          selection: true,
          search: false,
          toolbar: false,
          headerStyle: {
            backgroundColor: theme.palette.secondary.main,
            borderBottom: `1px solid ${theme.palette.secondary.main}`,
            paddingRight: 0,
            paddingLeft: 0,
        },
        sorting: false,
          rowStyle: {
              
          },

          cellStyle: {
            borderBottom: `1px solid ${theme.palette.secondary.main}`,
            fontWeight: 300,
            paddingRight: 0,
            paddingLeft: 0,
            fontSize: 14,
            paddingBottom: 12,
            paddingTop: 12,
          },
          footerStyle: {
            backgroundColor: theme.palette.secondary.main,
          },
          paging: false,
        }}
        style={{
          backgroundColor: theme.palette.primary.main,
            fontSize: '0.875rem!important'
        }}
        tableRef={tableRef}
        onSelectionChange={(rows) => {
          console.log(rows);
          configShopSetSelectedRows(rows, page);
        }}

        {...props}
      />
    );
  });

  let data;
  // console.log(configShopInitialData);

  if (page === 'shop') {
    data =
      filteredConfigShopData.length === 0 &&
      Object.keys(configShopfilters).length === 0
        ? tableData
        : filteredConfigShopData;
  }
  if (page === 'purchased') {
    data =
      filteredConfigShopPurchasedData.length === 0 &&
      Object.keys(configShopPurchasedfilters).length === 0
        ? tableData
        : filteredConfigShopPurchasedData;
  }


  const createTableData = (data) => {
    return data.map((item) => {
      return {
        id: item.id,
        device: item.device,
        os: item.os,
        browser: item.browser,
        screen: item.screen.resolution,
        useragent: item.useragent.name,
        videocard: item.videocard.name,
        created: item.created.date,
        price: item.price,
      };
    });
  };

  return (
    <Fragment>
      <StyledMaterialTable
        columns={[
          {
            title: 'Device, OS, Browser',
            field: 'device',
            width: 220,

            render: (rowData) => (
              <div style={{ display: 'flex', alignItems: 'center' }}>
                <img
                  src={rowData.device.icon}
                  alt="icon"
                  style={{ width: 20, height: 20 }}
                />
                <span
                  style={{
                    paddingLeft: 5,
                    paddingRight: 20,
                    fontSize: '0.875rem',
                    width: 70,
                  }}
                >
                  {rowData.device.name[0].toUpperCase() +
                    rowData.device.name.substring(1)}
                </span>
                <img
                  src={rowData.os.icon}
                  alt="icon"
                  style={{ width: 20, height: 20 }}
                />
                <span
                  style={{
                    paddingLeft: 5,
                    paddingRight: 20,
                    width: '40px',
                    fontSize: '0.875rem',
                  }}
                >
                  {rowData.os.version}
                </span>
                <img
                  src={rowData.browser.icon}
                  alt="icon"
                  style={{ width: 20, height: 20 }}
                />
                <span
                  style={{
                    paddingLeft: 5,
                    paddingRight: 20,
                    width: '40px',
                    fontSize: '0.875rem',
                  }}
                >
                  {rowData.browser.version}
                </span>
              </div>
            ),
          },
          { title: 'Screen', field: 'screen' },
          { title: 'Useragent', field: 'useragent' },
          { title: 'Videocard', field: 'videocard' },
          {
            title: 'Created',
            field: 'created',
          },
          {
            title: 'Price',
            field: 'price',
            render: (rowData) => (
              <span>
                ${' '}
                <span style={{ fontSize: '0.875rem', color: 'white' }}>
                  {rowData.price}
                </span>
              </span>

            ),
          },
        ]}
        data={createTableData(data)}
      />
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  filteredConfigShopData: state.configShop.filteredConfigShopData,
  configShopfilters: state.configShop.configShopfilters,
  filteredConfigShopPurchasedData:
    state.configShop.filteredConfigShopPurchasedData,
  configShopPurchasedfilters: state.configShop.configShopPurchasedfilters,
});

const mapDispatchToProps = (dispatch) => ({
  configShopSetSelectedRows: (selectedRowsArr, page) =>
    dispatch(configShopSetSelectedRows(selectedRowsArr, page)),
    resetFilters: () => dispatch(resetFilters())

});

export default connect(mapStateToProps, mapDispatchToProps)(CustomTable);
