import { appTypes} from './app.types'

export const setCurrentLanguage = lang => ({
  type: appTypes.SET_CURRENT_LANGUAGE,
  payload: lang
});

export const setLanguageModal = bool => ({
  type: appTypes.SET_LANGUAGE_MODAL,
  payload: bool
});

export const setChatState = bool => ({
  type: appTypes.SET_CHAT_STATE,
  payload: bool
});

export const setChatId = bool => ({
  type: appTypes.SET_CHAT_ID,
  payload: bool
})