export const getDate = (date) => {
  const monthNames = [

    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];

  let year = date.getFullYear(),
    month = date.getMonth(),
    day = date.getDate(),
    hours = date.getHours(),
    minutes =
      date.getMinutes().toString().length === 1
        ? `0${date.getMinutes()}`
        : date.getMinutes(),
    seconds =
      date.getSeconds().toString().length === 1
        ? `0${date.getSeconds()}`
        : date.getSeconds();

  return `${day} ${monthNames[month].substring(
    0,
    3
  )}, ${hours}:${minutes}:${seconds}`;
};
