import { configShopActionTypes } from './config-shop.types';

import { DATA } from './config-shop.data';

const INITIAL_STATE = DATA;

const configShopReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case configShopActionTypes.SET_FILTERS_FOR_CONFIG_SHOP_DATA:
      return setFiltersForConfigShopData(state, action.payload);

    case configShopActionTypes.FILTER_CONFIG_SHOP_DATA:
      return filterConfigShopData(state, action.payload);
      
      case configShopActionTypes.CONFIG_SHOP_SET_SELECTED_ROWS:
        return configShopSetSelectedRows(state, action.payload);

        case configShopActionTypes.RESET_FILTERS:
        return resetFilters(state, action.payload);

        case configShopActionTypes.RESET_SELECTED_DATA:
        return resetSelectedData(state, action.payload);

    default:
      return state;
  }
};

const resetSelectedData = (state, payload) => {
  return {
    ...state,
    selectedConfigShopItems:[],
    selectedConfigShopPurchasedItems:[]
  }
}

const resetFilters = (state, payload) => {
  return {
    ...state,
    configShopfilters:{},
    configShopPurchasedfilters:{},
    filteredConfigShopData:[],
    filteredConfigShopPurchasedData:[],
    selectedConfigShopItems:[],
    selectedConfigShopPurchasedItems:[]
  }
}

const configShopSetSelectedRows = (state, payload) => {
  console.log('payload configShopSetSelectedRows = ', payload)
  let selectedItems;
  let selectedRow;

  if (payload.page === 'shop') {
    selectedItems = 'selectedConfigShopItems';
    selectedRow = payload.selectedRows
  }
  if (payload.page === 'purchased') {
    selectedItems = 'selectedConfigShopPurchasedItems';
    selectedRow = payload.selectedRows
  }

  return {
    ...state,
    [selectedItems]: selectedRow
  }
}

const setFiltersForConfigShopData = (state, payload) => {

  console.log('payload = ', payload);
  let filtersCopy;
  let filtersObject;

  if(payload.category !== 'version') {

    if(payload.path === '/config-shop') {
      console.log('/config-shop');   
      filtersCopy = { ...state.configShopfilters};
      filtersObject = 'configShopfilters'
    }
  
    if(payload.path === '/purchased') {
      console.log('/purchased');
      filtersCopy = { ...state.configShopPurchasedfilters};
      filtersObject = 'configShopPurchasedfilters'
    }
  
    if(payload.filterBy === 'All') {
      delete filtersCopy[payload.category]
    } else {
      if(payload.path === '/config-shop') {
        filtersCopy = { ...state.configShopfilters, [payload.category]: payload.filterBy };
      }
      if(payload.path === '/purchased') {
        filtersCopy = { ...state.configShopPurchasedfilters, [payload.category]: payload.filterBy };
      }
  
    }
    return {
      ...state,
      [filtersObject]: filtersCopy,
    };
  } else {
    return {
      ...state
    }
  }

  
};

const filterConfigShopData = (state, payload) => {
  let configShopDataCopy;
  let filtersCopy;
  let filteredCSData;

  console.log('payload = ', payload);

  if(payload === '/config-shop') {
    filtersCopy = { ...state.configShopfilters};
    configShopDataCopy = [...state.configShopInitialData];
    filteredCSData = 'filteredConfigShopData';
    console.log('configShopDataCopy = ', configShopDataCopy)
  }
  if(payload === '/purchased') {
    filtersCopy = { ...state.configShopPurchasedfilters };
    configShopDataCopy = [...state.configShopPurchasedItems];
    filteredCSData = 'filteredConfigShopPurchasedData';
    console.log('configShopDataCopy purchased = ', configShopDataCopy)
  }

  // console.log('configShopDataCopy = ', configShopDataCopy);
  // console.log('filters = ', filters);

  const filteredData = configShopDataCopy.filter((item) => {
    let key = '';
    let value = '';

    let isTrue = true;

    if (Object.keys(filtersCopy).length > 0) {
      for (let filterKey in filtersCopy) {
        let checkIfTrue = true;
        key = filterKey;
        value = filtersCopy[filterKey];
        checkIfTrue = item[key].name === value;
        if(!checkIfTrue) { isTrue = false }
      }
    }
    return isTrue;
  });

  return {
    ...state,
    [filteredCSData]: filteredData

  };
};

export default configShopReducer;
