import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import Menu from '@material-ui/core/Menu';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

import {
  setFiltersForConfigShopData,
  filterConfigShopData,
  resetSelectedData

} from './../../redux/config-shop/config-shop.actions';

const StyledMenu = withStyles({
  paper: {
    border: '0px solid #d3d4d5',
    minWidth: '170px',
    borderRadius: 0,
  },
  list: {
    padding: '0',
  },

})((props) => (
  <Menu
    elevation={1}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));

const StyledVersionMenu = withStyles({
  paper: {
    border: '0px solid #d3d4d5',
    minWidth: '105px',
    borderRadius: 0,
  },
  list: {
    padding: '0',
  },
})((props) => (
  <Menu
    elevation={1}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.primary.main,
    '&:hover': {
      backgroundColor: 'grey',
    },
  },
}))(MenuItem);

const useStyles = makeStyles({
  root: {
    paddingRight: '10px',
  },
  select: {
    padding: '5px',
    paddingRight: '1px',

  },
  version: {
    paddingRight: '10px',
    paddingLeft: 0,
  },
  menuItem: {
    justifyContent: 'center',
  },
  table: {
    minWidth: 650,
  },
});

const createSelectitemsByType = (initialArray, filterBy) => {
  const newObject = initialArray.reduce((items, item) => {

    const {
      [filterBy]: { name },
    } = item;
    items[name] = items[name] ? { ...items[name] } : item[filterBy];
    return items;
  }, {});

  let newArr = [];
  for (let key in newObject) {
    newArr.push(newObject[key]);
  }
  newArr.unshift({ name: 'All', category: filterBy });

  return newArr;
};

const CustomSelect = ({
  configShopData,
  filterByType,
  dataForFiltering,
  setFiltersForConfigShopData,
  filterConfigShopData,
  location,
                        resetSelectedData
}) => {

  const selectItems = createSelectitemsByType(dataForFiltering, filterByType);
  const [currentIcon, setCurrentIcon] = React.useState(selectItems[0].icon);
  const [currentSelectItemName, setCurrentSelectItemName] = React.useState(
    'ALL'
  );

  console.log('dataForFiltering = ', dataForFiltering);
  console.log('location = ', location.pathname);

  const handleMenuItemClick = (event, filterBy, category, version) => {
    if (category !== 'version') {
      const currentItem = selectItems.find((item) => item.name === filterBy);
      let path = location.pathname;

      console.log('filterBy = ', filterBy);
      console.log('category = ', category);
      console.log('category = ', version);

      if (path === '/config-shop') {
        setFiltersForConfigShopData({ filterBy, category, path, version });
        setCurrentIcon(currentItem.icon);
        setCurrentSelectItemName(currentItem.name);
        filterConfigShopData(path);
      }
      if (path === '/purchased') {
        setFiltersForConfigShopData({ filterBy, category, path });
        setCurrentIcon(currentItem.icon);
        setCurrentSelectItemName(currentItem.name);
        filterConfigShopData(path);
      }
      resetSelectedData()

    }
  };

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [versionAnchorEl, setVersionAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleVersionClick = (event) => {
    setVersionAnchorEl(event.currentTarget);
  };
  const handleVersionClose = () => {
    setVersionAnchorEl(null);
  };

  const checkVersionField = (data) => {
    let versions = {};
    console.log('data = ', data);
    console.log('filterByType = ', filterByType);

    for (let item of data) {
      if (item[filterByType].hasOwnProperty('version')) {
        let curVersion = item[filterByType].version;

        if (!versions[filterByType]) {
          versions[filterByType] = [];
        } else {
          let findVersion = versions[filterByType].find((ver) => {
            return ver === curVersion;
          });
          if (!findVersion) {
            versions[filterByType].push(curVersion);
          }
        }
        // console.log('versions = ', versions);
      }
    }
    return versions;
  };

  const hasVersionsMenu =
    Object.keys(checkVersionField(configShopData)).length == 0;

  return (
    <Fragment>
      {hasVersionsMenu ? (
        <div className={classes.root}>
          <Button
            aria-controls="customized-menu"
            aria-haspopup="true"
            variant="contained"
            color="primary"
            style={{
              minWidth: '170px',
              borderRadius: '5px 5px 0 0',
              borderBottom: '1px solid grey',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
            }}
            onClick={handleClick}
            startIcon={
              <span
                style={{
                  background: `url(${currentIcon})`,
                  height: '30px',
                  width: '30px',
                  paddingRight: '10px',
                }}
              ></span>
            }
            endIcon={<ArrowDropDownIcon />}
          >
            <div>
              <p
                style={{
                  textTransform: 'Capitalize',
                  marginBottom: 0,
                  fontSize: '0.875rem',
                }}
              >
                {filterByType}
              </p>

              <p
                style={{
                  textTransform: 'Capitalize',
                  marginBottom: 0,
                  color: 'white',
                }}
              >
                {currentSelectItemName}
              </p>
            </div>
          </Button>
          <StyledMenu
            id="customized-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            {selectItems.map((option) => {
              return (
                <StyledMenuItem
                  key={option.name}
                  onClick={(e) => {
                    handleMenuItemClick(e, option.name, option.category);
                    handleClose();
                  }}
                  value={option.name}
                >
                  <span
                    style={{
                      background: `url(${option.icon})`,
                      height: '30px',
                      width: '30px',
                      marginRight: '20px',
                    }}
                  ></span>
                  {option.name}
                </StyledMenuItem>
              );
            })}
          </StyledMenu>
        </div>
      ) : (
        <Fragment>
          <div className={classes.select}>
            <Button
              aria-controls="complex-menu"
              aria-haspopup="true"
              variant="contained"
              color="primary"
              style={{
                minWidth: '170px',
                borderRadius: '5px 5px 0 0',
                borderBottom: '1px solid grey',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}
              onClick={handleClick}
              startIcon={
                <span
                  style={{
                    background: `url(${currentIcon})`,
                    height: '30px',
                    width: '30px',
                    paddingRight: '10px',
                  }}
                ></span>
              }
              endIcon={<ArrowDropDownIcon />}
            >
              <div>
                {filterByType === 'os' ? (
                  <p style={{ marginBottom: 0, fontSize: '0.875rem' }}>
                    {filterByType}
                  </p>
                ) : (
                  <p
                    style={{
                      textTransform: 'Capitalize',
                      marginBottom: 0,
                      fontSize: '0.875rem',
                    }}
                  >
                    {filterByType}
                  </p>
                )}
                <p
                  style={{
                    textTransform: 'Capitalize',
                    marginBottom: 0,
                    color: 'white',
                  }}
                >
                  {currentSelectItemName}
                </p>
              </div>
            </Button>
            <StyledMenu
              id="complex-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              {selectItems.map((option) => {
                return (
                  <StyledMenuItem
                    key={option.name}
                    onClick={(e) => {
                      handleMenuItemClick(e, option.name, option.category);
                      handleClose();
                    }}
                    value={option.name}
                  >
                    <span
                      style={{
                        background: `url(${option.icon})`,
                        height: '30px',
                        width: '30px',
                        marginRight: '20px',
                      }}
                    ></span>
                    {option.name}
                  </StyledMenuItem>
                );
              })}
            </StyledMenu>
          </div>
          <div className={classes.version}>
            <Button
              aria-controls="version-menu"
              aria-haspopup="true"
              variant="contained"
              color="primary"
              style={{
                minWidth: '100px',
                height: '63px',
                borderRadius: '5px 5px 0 0',
                borderBottom: '1px solid grey',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}
              onClick={handleVersionClick}
              endIcon={<ArrowDropDownIcon />}
            >
              <div>
                <p
                  style={{
                    textTransform: 'Capitalize',
                    marginBottom: 0,
                    fontSize: '0.875rem',
                  }}
                >
                  {'version'}
                </p>

                <p
                  style={{
                    textTransform: 'Capitalize',
                    marginBottom: 0,
                    color: 'white',
                  }}
                >
                  {checkVersionField(configShopData)[filterByType][0]}
                </p>
              </div>
            </Button>
            <StyledVersionMenu
              id="version-menu"
              anchorEl={versionAnchorEl}
              keepMounted
              open={Boolean(versionAnchorEl)}
              onClose={handleVersionClose}
            >
              {checkVersionField(configShopData)[filterByType].map(
                (version) => {
                  console.log('version = ', version);

                  return (
                    <StyledMenuItem
                      key={version}
                      className={classes.menuItem}
                      onClick={(e) => {
                        handleMenuItemClick(
                          e,
                          filterByType,
                          'version',
                          version
                        );
                        handleVersionClose();
                      }}
                      value={version}
                    >
                      {version}
                    </StyledMenuItem>
                  );
                }
              )}

            </StyledVersionMenu>
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  configShopData: state.configShop.configShopInitialData,
});

const mapDispatchToProps = (dispatch) => ({
  setFiltersForConfigShopData: (filterBy) =>
    dispatch(setFiltersForConfigShopData(filterBy)),
  filterConfigShopData: (path) => dispatch(filterConfigShopData(path)),
  resetSelectedData: () => dispatch(resetSelectedData())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(CustomSelect));

