import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';

import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

import CustomSelect from '../UI/CustomSelect';
import CustomSearch from '../UI/CustomSearch';
import CustomTable from '../UI/CustomTable';
import TabsManager from '../TabsManager/TabsManager';
import { configShopSetSelectedRows } from '../../redux/config-shop/config-shop.actions';

const ConfigShop = ({
  configShopInitialData,
  selectedConfigShopItems,
  configShopSetSelectedRows,
}) => {

  const theme = useTheme();
  const useStyles = makeStyles({
    root: {
      paddingBottom: 0,
      position: 'relative',
      display: 'flex',
      flexDirection: 'column',
      // justifyContent: 'space-around',
      minHeight: 'calc(100vh - 86px)',
    },
    table: {
      padding: '20px',
      paddingBottom: 1,

    },
    selectsWrapper: {
      paddingLeft: 20,
      paddingRight: 20,
      display: 'flex',
      alignItems: 'center',
    },

    selectedItems: {
      backgroundColor: theme.palette.secondary.main,
      minHeight: 60,
      marginTop: 'auto',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',

    },
    cancelRoot: {
      marginLeft: 20,
    },
    cancelLabel: {
      color: 'white',
    },
    selectedRoot: {
      marginRight: 20,
      marginLeft: 20,
      backgroundColor: '#A1AFDB',

      border: '2px solid transparent',
      '&:hover': {
        border: '2px solid #A1AFDB',
        '& .MuiButton-label': {
          color: '#A1AFDB',
        },
      },

    },
    selectedLabel: {
      color: '#2c2f38',
      fontWeight: 400,
    },
  });

  let selectedItemsPrice = selectedConfigShopItems.reduce((sum, item) => {
    return sum + item.price;
  }, 0);

  const classes = useStyles();
  let tabs = [
    { name: 'Shop', link: '/config-shop' },
    { name: 'Purchased', link: '/purchased' },
  ];

  return (
    <Fragment>
      <TabsManager data={tabs} />
      <div className="config-shop">
        <div className={classes.root}>
          <div className={classes.selectsWrapper}>
            <CustomSelect
              dataForFiltering={[...configShopInitialData]}
              filterByType={'device'}
            />
            <CustomSelect
              dataForFiltering={[...configShopInitialData]}
              filterByType={'os'}
            />
            <CustomSelect
              dataForFiltering={[...configShopInitialData]}
              filterByType={'browser'}
            />
            <CustomSearch></CustomSearch>
          </div>
          <div className={classes.table}>
            <CustomTable
              tableData={configShopInitialData}
              page={'shop'}
            ></CustomTable>
          </div>
          {selectedConfigShopItems.length > 0 ? (
            <div className={classes.selectedItems}>
              <div>
                <Button
                  variant="outlined"
                  size="small"
                  classes={{
                    root: classes.cancelRoot,
                    label: classes.cancelLabel,
                  }}
                >
                  Cancel
                </Button>
              </div>
              <div>
                <span
                  style={{ fontWeight: 400, color: 'white', paddingRight: 5 }}
                >
                  {selectedConfigShopItems.length}
                </span>
                selected for
                <span
                  style={{ fontWeight: 400, color: 'white', paddingLeft: 5 }}
                >
                  ${selectedItemsPrice.toFixed(2)}
                </span>
                <Button
                  size="small"
                  classes={{
                    root: classes.selectedRoot,
                    label: classes.selectedLabel,
                  }}
                >
                  Buy Selected
                </Button>
              </div>
            </div>
          ) : null}
        </div>
      </div>

    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  configShopInitialData: state.configShop.configShopInitialData,
  selectedConfigShopItems: state.configShop.selectedConfigShopItems,

});

const mapDispatchToProps = (dispatch) => ({
  configShopSetSelectedRows: (selectedRowsArr) =>
    dispatch(configShopSetSelectedRows(selectedRowsArr)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ConfigShop);
