import {configShopActionTypes} from './config-shop.types';

export const setFiltersForConfigShopData = (filterBy) => ({
  type: configShopActionTypes.SET_FILTERS_FOR_CONFIG_SHOP_DATA,
  payload: filterBy
})
export const filterConfigShopData = (path) => ({
  type: configShopActionTypes.FILTER_CONFIG_SHOP_DATA,
  payload: path
})

export const configShopSetSelectedRows =(selectedRows, page) => ({
  type: configShopActionTypes.CONFIG_SHOP_SET_SELECTED_ROWS,
  payload: {selectedRows, page}
})

export const resetFilters = () => ({
  type: configShopActionTypes.RESET_FILTERS
})

export const resetSelectedData = () => ({
  type: configShopActionTypes.RESET_SELECTED_DATA

})